<template>
    <section id="blog">
        <ul class="blog--category">
            <li :class="{ 'on': categoryType  === 0 }">
                <button type="button" @click="categotyButton($event, 0)">#IT</button>
            </li>
            <li :class="{ 'on': categoryType  === 1 }">
                <button type="button"  @click="categotyButton($event, 1)">#독서</button>
            </li>
        </ul>
    </section>
 </template>
       
 <script>
 import { getCurrentInstance } from 'vue'
 import { computed, ref, onMounted, watch } from 'vue';
 import { gsap, power3 } from 'gsap';
 
 
 
 export default {
     name: 'tabButton',
     props: {
        responseData: {
            type: Array,
            required: true
        },
        onChildData:{
            type:Blob,
            required: true
        }
    },
  
     setup(props) {

        const categoryType = ref(0);
        const storedTabProps = localStorage.getItem('tabTypeProps');
        const typeNumCheck = ref(null);

   

        const categotyButton = ( event, typeNumCheck ) => {
            if( categoryType.value === typeNumCheck ) return;
            categoryType.value = typeNumCheck;
            localStorage.setItem('tabTypeProps', categoryType.value.toString());
            props.onChildData(categoryType.value);
        }

        const clickIndexCheck = () => {
            if (storedTabProps !== null) {
                categoryType.value = parseInt(storedTabProps);
                console.log( "여기타니111?" );
            }
        }

        onMounted(() => {
            clickIndexCheck();
        })

        return {
            categotyButton,
            categoryType,
        }; 
     }
 }
 </script>  
 <style lang="scss" scoped>
 @import "@/assets/scss/__variables.scss";

 .blog--category{
        display: flex;
        justify-content: center;
        justify-items: center;
        @include breakDevicePoint( margin,  vw(10px 0), null, null ); 
        li{
            display: inline-flex;
            @include breakDevicePoint( margin,  vw(0 10px), null, null ); 
            @include breakDevicePoint( padding,  vw(10px 20px), null, null ); 
            @include breakDevicePoint( border-radius,  vw(25px), null, null ); 
            button{
                display: block;
                @include breakDevicePoint( font-size,  vw(48px), vw(48px), null ); 
            }
            &.on{
                background: $dpColor;
                button{
                    color: #fff;
                }
            }
        }
    }
 </style>
       