<template>
    <section id="blog">
        <div class="inner">
            <h2>BLOG</h2>
            <p class="blog--word">현재 전체 등록된 글 <em>{{count}}</em>개입니다.</p>
            <TabButton  :onChildData="handleChildData" />
            <template v-if="isLoading" >
                <Skeletor :SkeletorHeight="300"  :SkeletorLength="5" :SkeletorLocation="`card`"  :SkeletorType="`list`"/>
            </template>
            <template v-else>
                <BlogList :responseData="resultResponseArr" :blogInfoData="blogInfoItem" :typeCheckProps="selectTypeProps" />
            </template>
           
        </div>
    </section>
</template>
  
<script>

import { ref, defineProps, watch,  onMounted, onBeforeUnmount, computed  } from 'vue';
import { gsap, Expo } from 'gsap';
import TabButton from '@/components/Layouts/buttons/TabButton.vue'
import BlogList from '@/components/Layouts/List/BlogList.vue'
import axios from 'axios'
import Skeletor from "@/components/Layouts/common/Skeleton.vue";


export default {
    name: 'Blog',
    defineProps: {
        responseData: {
            type: Array,
            required: true
        },
        blogInfoData: {
            type: Array,
            required: true
        },
    },
   components: {
        TabButton,
        BlogList,
        Skeletor
    },

    setup(props) {

        const receivedData = ref('');
        const responseCategoryItems = ref([]);
        const responseListItems = ref([]);
        const blogInfoItem = ref([]);
        const resultResponseArr = ref([]);
        const count = ref( 0 );
        const ACCESS_TOKEN = "88f0ff4e5c7f1fca1ceee0437e6771d5_e4db11b9a579e41ca6dde90025c7e146";
        const num = {score:0};
        const selectTypeProps = ref(0);
        const storedTypeProps = localStorage.getItem('selectTypeProps');
        const isLoading = ref(false);

        if (storedTypeProps !== null) {
            selectTypeProps.value = parseInt(storedTypeProps);
        }

        const handleChildData = (category) => {
            receivedData.value = category;
            selectTypeProps.value = receivedData.value;
            localStorage.setItem('selectTypeProps', selectTypeProps.value.toString());
            fetchData(category);
        };

        const tstoryCategoryList = async () => {
            try {
                const response = await axios.get("https://www.tistory.com/apis/category/list?", {
                    params: {
                        access_token: ACCESS_TOKEN,
                        output: 'json',
                        blogName: 'rico86',
                    },
                })
                responseCategoryItems.value = response.data.tistory.item.categories;
                // console.log( "카테고리===>", responseCategoryItems.value);

            } catch (error) {
                console.error('An error occurred:', error);
            }
        };

        const tstoryItemList = async () => {
            try {
                const response = await axios.get("https://www.tistory.com/apis/post/list?", {
                    params: {
                        access_token: ACCESS_TOKEN,
                        output: 'json',
                        blogName: 'rico86',
                        // page: '1',
                    },
                })
                
                responseListItems.value = response.data.tistory.item.posts;
                // console.log( "리스트===>", responseListItems.value);
                count.value =  response.data.tistory.item.totalCount;
            } catch (error) {
                console.error('An error occurred:', error);
            }
        };

        const countValue = ( count ) => {
              gsap.to( num, 1, {score: `+=${count}`, roundProps:"score", ease:Expo.easeInOut, onUpdate:upDateHandler});
        }

        const upDateHandler = () => {
            if( count.value == null) return;
            count.value = num.score;
        }

        const tstoryInfo = async () => {
            try {
                const response = await axios.get("https://www.tistory.com/apis/blog/info?", {
                    params: {
                        access_token: ACCESS_TOKEN,
                        output: 'json',
                    },
                });

                const blogImg =  response.data.tistory.item.blogs[0].profileImageUrl;
                const blogName =  response.data.tistory.item.blogs[0].nickname;
                const blogDescription =  response.data.tistory.item.blogs[0].description;

                blogInfoItem.value.push( blogImg, blogName, blogDescription );
                
            } catch (error) {
                console.error('An error occurred:', error);
            }
        };

        const fetchData = async (category) => {
            try{
                if( category == undefined ) category = 0;
                const combinedArray = responseListItems.value.map(post => ({
                    ...post,
                    info: responseCategoryItems.value.find(category => category.id === post.categoryId),
                }));
                if( selectTypeProps.value == 0 ){
                    const filteredArray = combinedArray.filter(item => item.info.id !== '1158284');
                    resultResponseArr.value = filteredArray;
                }else{
                    const filteredArray = combinedArray.filter(item => item.info.id === '1158284');
                    resultResponseArr.value = filteredArray;
                }
            }catch( error ){
                console.error('An error occurred:', error);
            }finally{
                    isLoading.value = false;
            }
           

        };

        onMounted( async () => {

            try {
                isLoading.value = true;
                await Promise.all([tstoryCategoryList(), tstoryItemList(), tstoryInfo()]);
                await fetchData();
            } finally {
                isLoading.value = false;
            }
        
        });

        onBeforeUnmount(() => {
            tstoryCategoryList();
            tstoryItemList();
            // tst5oryInfo();
            countValue();
            upDateHandler();
        });
        
        return {
            responseCategoryItems,
            count,
            blogInfoItem,
            resultResponseArr,
            receivedData,
            tstoryCategoryList,
            handleChildData,
            selectTypeProps,
            fetchData,
            isLoading
        };
    }
}
</script>
  
<style lang="scss" scoped>
@import "@/assets/scss/__variables.scss";

#blog { 
    .inner {
        display: block;
        @include breakDevicePoint( padding,  vw(200px 0), vw(400px 0), null ); 
        h2 {
            text-align: center;
            font-weight: bold;
            color: $dpColor;
            padding-bottom: vw(20px);
            // @include breakDevicePoint( padding-top, vw(200px), vw(400px), null ); 
            @include breakDevicePoint( font-size, vw(120px), vw(180px), null ); 
        }

        .blog--word {
            text-align: center;
            font-family: 'Noto Sans Korean';
            font-weight: 600;
            color: #181818;
            @include breakDevicePoint(padding, vw(20px 0), null, null ); 
            @include breakDevicePoint(  font-size,  vw(80px),vw(120px), null ); 
            
            >em {
                @include breakDevicePoint(  font-size,  vw(80px),vw(120px), null ); 
                color: $dpColor;

            }
        }

    }
}
</style>
  